#root {
  box-sizing: border-box;
  font-size: 0.785em;
}


/* RESET */
*,
::after,
::before {
  margin: 0;
  border: 0;
  padding: 0;
  box-sizing: inherit;
  outline: none 0;
}
body * {
  font-family: "Roboto", sans-serif;
  font-weight: 200;
  font-style: normal;
}

h1 {
  font-size: 3.5rem;
}
h2 {
  font-size: 2.25rem;
}
h3 {
  font-size: 1.5rem;
}
h4 {
  font-size: 1.1rem;
}
h5 {
  font-size: 0.9rem;
}
h6 {
  font-size: 0.7rem;
}
